import React, {useState} from 'react';
import './app.css';
import Peg from './peg'

function App() {
  const [pegSize, setPegSize] = useState(50);
  const [pegRad, setPegRad] = useState(4.5);
  const [numRows, setNumRows] = useState(10);
  const [numCols, setNumCols] = useState(10);
  const [options, toggleOptions] = useState(false);
  let grid = [];

  function buildGrid() {
    for (let i=0; i<numRows; i++){
      let row = [];
      for (let k=0; k<numCols; k++){
        row.push(`${i},${k}`);
      }
      grid.push(row);
    }
  }

  return (
    <div className="App">
    <button className="toggle" onClick={()=>toggleOptions(true)}>options</button>
      <form onChange={buildGrid()} id="options" style={{display: options?"flex":"none"}}>
        <label>peg space<input type="number" value={pegSize} onChange={(e) => {setPegSize(e.target.value)}} min="2"/></label>
        <label>peg radius<input type="range" value={pegRad} onChange={(e) => {setPegRad(e.target.value)}} min="0" max="10"/></label>
        <label>rows<input type="number" value={numRows} onChange={(e) => {setNumRows(e.target.value)}} min="1"/></label>
        <label>columns<input type="number" value={numCols} onChange={(e) => {setNumCols(e.target.value)}} min="1"/></label>
        <button className="toggle" onClick={()=>toggleOptions(false)}><strong>X</strong></button>
      </form>
      <main style={{
        display : "grid",
        gridTemplateColumns: `repeat(${numCols}, ${pegSize}px)`
        }}>
        {grid.map((row)=> row.map((col)=> <Peg rad={pegRad} key={col} size={pegSize}/>))}
      </main>
    </div>
  );
}

export default App;
