import React,{useState} from 'react';

function Peg(props){
  const [hue, setHue] = useState(0);
  const [light, setLight] = useState(50);

  let colors = `hsl(${hue}, 100%, ${light}%)`;
  function handleClick (){
    console.log("ayyyee lit fam");
    if( hue <= 360 && light === 50 ){
      setHue(hue+45)
    }else{
      if(light === 50) {setLight(100);}
      else if(light===100){setLight(0);}
      else if(light === 0){
        setLight(50);
        setHue(0);
      }

    }
  }

  return(
  <svg height={props.size} width={props.size} onClick={()=>handleClick()} fill={colors}>
    <circle cx={props.size * .5} cy={props.size * .5} r={props.size * (props.rad/10)}  />
  </svg>
  );
}

export default Peg;
